.popup {
  z-index: 999;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.popup.active {
  pointer-events: auto;
}

.popup.active .popup__overlay {
  opacity: 1;
}

.popup.active .popup__main {
  opacity: 1;
  transform: translateY(0);
}

.popup.active .popup__main-head, .popup.active .popup__main-submit {
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  transition-delay: .2s;
}

.popup.active .popup__main-form-inner {
  pointer-events: auto;
}

.popup.active .popup__main-form-inner.hidden {
  pointer-events: none;
}

.popup.active .popup__main-form-success {
  pointer-events: auto;
}

.popup.active .popup__main-form-success.hidden {
  pointer-events: none;
}

.popup__overlay {
  opacity: 0;
  background-color: #151515f2;
  width: 100%;
  height: 100%;
  transition: opacity .4s;
  position: absolute;
  top: 0;
  left: 0;
}

.popup__main {
  opacity: 0;
  background-color: #ff471d;
  width: 100%;
  max-width: 108rem;
  margin-top: 12rem;
  margin-bottom: 6rem;
  padding: 6rem 6rem 0;
  transition: all .4s;
  position: relative;
  transform: translateY(10%);
}

.popup__main-form {
  grid-template-columns: 4.1fr 5.9fr;
  display: grid;
}

.popup__main-head {
  clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
  background-color: #fff;
  flex-direction: column;
  grid-column: 1 / 2;
  align-items: start;
  margin-top: -12rem;
  padding: 6rem;
  transition: all .4s;
  display: flex;
  overflow: hidden;
}

.popup__main-head-title {
  margin-bottom: 4rem;
  margin-left: -1rem;
}

.popup__main-head-txt {
  color: #4b4b4b;
}

.popup__main-contact {
  flex-direction: column;
  grid-area: 2 / 1 / 3 / 2;
  justify-content: end;
  align-items: start;
  padding-bottom: 4.8rem;
  display: flex;
}

.popup__main-contact-label, .popup__main-contact-link {
  color: #fff;
}

.popup__main-form-inner {
  opacity: 1;
  flex-direction: column;
  grid-area: 1 / 2 / 2 / 3;
  transition: all .4s;
  display: flex;
  transform: none;
}

.popup__main-form-inner.hidden {
  opacity: 0;
  transform: translateY(3.2rem);
}

.popup__main-form-inner .input-wrap {
  width: calc(100% - 6rem);
  margin-left: 6rem;
  position: relative;
}

.popup__main-form-inner .input-wrap:hover .input-field::placeholder, .popup__main-form-inner .input-wrap:focus .input-field::placeholder {
  color: #ffffffb3;
}

.popup__main-form-inner .input-wrap:hover .input-field-line, .popup__main-form-inner .input-wrap:focus .input-field-line {
  transform: scaleX(1);
}

.popup__main-form-inner .input-field {
  color: #fff;
  border: none;
  border-bottom: 1px solid #fff3;
  padding: 2.4rem 0;
}

.popup__main-form-inner .input-field::placeholder {
  color: #ffffff80;
  transition: color .4s;
}

.popup__main-form-inner .textarea-wrap {
  flex-grow: 1;
  max-height: 25rem;
}

.popup__main-form-inner textarea.input-field {
  resize: vertical;
  height: 100%;
}

.popup__main-form-inner .input-field-line {
  transform-origin: 0;
  background-color: #ffffff80;
  width: 100%;
  height: 1px;
  transition: transform .4s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.popup__main-form-success {
  color: #fff;
  opacity: 1;
  grid-area: 1 / 2 / 2 / 3;
  padding-bottom: 4rem;
  padding-left: 6rem;
  transition: all .4s;
  transform: none;
}

.popup__main-form-success.hidden {
  opacity: 0;
  transform: translateY(3.2rem);
}

.popup__main-submit {
  appearance: none;
  text-align: left;
  clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
  cursor: pointer;
  background-color: #fff;
  border: none;
  outline: none;
  width: 25rem;
  padding: 4rem;
  transition: all .4s;
  display: grid;
}

.popup__main-submit :hover .popup__main-submit-inner-txt {
  color: #ff471d;
}

.popup__main-submit-aspect {
  pointer-events: none;
  grid-area: 1 / 1 / 2 / 2;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
}

.popup__main-submit-inner {
  flex-direction: column;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: space-between;
  align-items: start;
  height: 100%;
  transition: all .4s;
  display: flex;
}

.popup__main-submit-inner .heading {
  color: #151515;
  transition: color .4s;
}

.popup__main-submit-inner .icon {
  color: #ff471d;
}

.popup__main-submit-inner-success {
  pointer-events: none;
  opacity: 0;
  height: calc(100% + 8rem);
  margin: -4rem;
  padding: 4rem;
  transform: translateY(2.4rem);
}

.popup__main-submit.on-complete {
  pointer-events: none;
}

.popup__main-submit.on-complete .popup__main-submit-inner-default {
  pointer-events: none;
  opacity: 0;
  transform: translateY(2.4rem);
}

.popup__main-submit.on-complete .popup__main-submit-inner-success {
  pointer-events: auto;
  opacity: 1;
  transform: translateY(0);
}

.popup__close {
  color: #ffffff80;
  padding: 1.4rem;
  transition: color .4s;
  position: absolute;
  top: 0;
  right: 0;
}

.popup__close:hover {
  color: #fff;
}

